.form-group {
	position: relative;
	&:not(:last-child) {
		margin-bottom: 20px; }
	&_sm {
		&:not(:last-child) {
			margin-bottom: 10px; } }
	&_inline,
	&_table {
		display: flex;
		align-items: center;
		.form-group {
			&__label {
				padding-right: 15px; }
			&__field {
				flex-grow: 1;
				min-width: 0; }
			&__delete {
				position: absolute;
				top: 0;
				right: 0;
				~ .form-group__field {
					padding-right: 60px; } } } }
	&_column {
		.form-group {
			&__field {
				width: 100%; } } }
	&_table {
		.form-group {
			&__label {
				flex-grow: 1;
				width: 33%; }
			&__field {
				width: 57%; } } }
	&__label {
		padding: 10px 0; }
	&__field {
		position: relative; }
	&__error-message {
		position: absolute;
		z-index: 10;
		left: 0;
		bottom: 100%;
		margin-bottom: 6px; } }
