body {
  min-width: 980px;
  color: #333;
  background: #f8f8f8;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

html,
body {
  height: 100%;
}

.out {
  display: flex;
  flex-direction: column;
  max-height: 100%;
  min-height: 600px;
}

.container {
  overflow: auto;
  flex-grow: 1;
  max-height: 100%;
}

.inner {
  padding: 20px;
  margin: 0 auto;
  max-width: 1024px;
  width: 100%;
}

[data-scrollbar]::-webkit-scrollbar {
  display: initial !important;
}

[data-scrollbar]::-webkit-scrollbar:vertical {
  width: 7px;
  background: linear-gradient(to right, transparent 3px, #5aaee2 3px, #5aaee2 4px, transparent 4px);
}

[data-scrollbar]::-webkit-scrollbar:horizontal {
  height: 7px;
  background: linear-gradient(to bottom, transparent 3px, #5aaee2 3px, #5aaee2 4px, transparent 4px);
}

[data-scrollbar]::-webkit-scrollbar-thumb {
  background: #2e99db;
}

.no-touch [data-scrollbar]::-webkit-scrollbar-thumb:hover {
  background: #1f7db7;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.heading {
  padding-bottom: 10px;
  margin-bottom: 15px;
  border-bottom: 1px solid rgba(51, 51, 51, 0.2);
  color: gray;
}

.description {
  color: #333;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.description:not(:last-child) {
  margin-bottom: 10px;
}

.label {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
}

.error-message {
  padding: 4px 7px;
  border: 1px solid #ec4a41;
  border-radius: 2px;
  max-width: 100%;
  opacity: 0;
  visibility: hidden;
  color: #fff;
  background: #ec4a41;
  transition: opacity 0.25s, visibility 0.25s;
}

.error-message:before {
  content: '';
  position: absolute;
  top: 100%;
  left: 15px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 5px 6px 0 6px;
  border-color: #ec4a41 transparent transparent transparent;
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-size: 19px;
  font-weight: 300;
  line-height: 60px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn:hover .icon-right {
  transform: translate3d(50%, 0, 0);
}

.btn:hover .icon-left {
  transform: translate3d(-50%, 0, 0);
}

.btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.btn .icon-right,
.btn .icon-left {
  display: inline-block;
  vertical-align: top;
  fill: currentColor;
  font-size: 80%;
  margin-top: 22px;
  transition: transform .175s;
}

.btn-delete {
  width: 60px;
  height: 60px;
  text-align: center;
  font-size: 0;
}

.btn-delete:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.btn-delete:active .icon {
  fill: #e32117;
}

.btn-delete .icon {
  display: inline-block;
  vertical-align: middle;
  fill: #ec4a41;
  font-size: 18px;
  transition: fill 0.25s;
}

.select {
  position: relative;
  user-select: none;
}

.select.is-active .select__arrow {
  transform: rotate(180deg);
}

.select.is-active .select__value {
  border-color: #2e99db;
}

.select__arrow {
  position: absolute;
  top: 50%;
  right: 15px;
  font-size: 8px;
  margin-top: -0.5em;
  pointer-events: none;
  transition: transform 0.25s;
}

.select__value {
  overflow: hidden;
  padding: 0 30px 0 15px;
  border: 1px solid #333;
  border-radius: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 58px;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: border-color 0.25s;
}

.select__dropdown {
  position: absolute;
  z-index: 200;
  top: 100%;
  left: 0;
  right: 0;
  overflow: auto;
  padding: 10px 0;
  margin-top: 10px;
  border: 1px solid #333;
  max-height: 200px;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  transition: border-color 0.25s, opacity 0.3s, visibility 0.3s, transform 0.3s;
}

.select__dropdown[data-position="true"] {
  margin-top: -10px;
}

.select__dropdown.is-open {
  border-color: #2e99db;
  opacity: 1;
  visibility: visible;
}

.select__item {
  overflow: hidden;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s, background 0.25s;
}

.select__item:hover {
  color: #2e99db;
}

.select__item.is-active {
  color: #fff;
  background: #2e99db;
}

.my-select select {
  width: 100%;
}

.my-select__arrow {
  position: absolute;
  top: 50%;
  right: 15px;
  font-size: 8px;
  margin-top: -0.5em;
  pointer-events: none;
  transition: transform 0.25s;
}

.my-select .select2-container--default .select2-selection--single {
  border-color: #333;
  border-radius: 0 !important;
  height: 60px;
  transition: border-color 0.25s;
}

.my-select .select2-container--default .select2-selection--single .select2-selection__rendered {
  padding: 0 30px 0 15px;
  color: #333;
  font-size: 16px;
  font-weight: 400;
  line-height: 58px;
}

.my-select .select2-container--default .select2-selection--single .select2-selection__arrow {
  display: none;
}

.my-select .select2-container--default .select2-results > .select2-results__options {
  padding: 10px 0;
}

.my-select .select2-container--open .select2-selection--single {
  border-color: #2e99db;
}

.my-select .select2-container--open ~ .my-select__arrow {
  transform: rotate(180deg);
}

.my-select .select2-dropdown {
  border: 1px solid #333 !important;
  border-radius: 0 !important;
}

.my-select .select2-dropdown.select2-dropdown--below {
  margin-top: 10px;
}

.my-select .select2-dropdown.select2-dropdown--above {
  margin-bottom: 10px;
}

.my-select .select2-results__option {
  overflow: hidden;
  padding: 5px 15px;
  height: auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s, background 0.25s;
}

.my-select .select2-results__option--highlighted[aria-selected] {
  color: #2e99db;
  background: none;
}

.my-select .select2-results__option[aria-selected=true] {
  color: #fff;
  background: #2e99db;
}

.input {
  padding: 0 15px;
  border: 1px solid #333;
  border-radius: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:focus {
  border-color: #2e99db;
}

.input:disabled {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.input.is-error ~ .error-message {
  opacity: 1;
  visibility: visible;
}

.input_sm {
  height: 40px;
}

.radio {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.radio:after {
  content: " ";
  display: table;
  clear: both;
}

.radio__input {
  display: none;
}

.radio__input:checked ~ .radio__icon {
  border-color: #2e99db;
}

.radio__input:checked ~ .radio__icon:before {
  opacity: 1;
}

.radio__input:disabled ~ .radio__icon, .radio__input:disabled ~ .radio__label {
  opacity: 0.7;
  cursor: not-allowed;
}

.radio__icon {
  position: relative;
  float: left;
  border: 1px solid #333;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background: #fff;
  cursor: pointer;
  transition: border-color 0.25s;
}

.radio__icon:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -0.5em 0 0 -0.5em;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  opacity: 0;
  background: #2e99db;
  font-size: 10px;
  transition: opacity 0.25s;
}

.radio__label {
  display: block;
  overflow: hidden;
  padding-left: 10px;
  color: #333;
  font-size: 18px;
  font-weight: 600;
  line-height: 20px;
  cursor: pointer;
}

.form-group {
  position: relative;
}

.form-group:not(:last-child) {
  margin-bottom: 20px;
}

.form-group_sm:not(:last-child) {
  margin-bottom: 10px;
}

.form-group_inline, .form-group_table {
  display: flex;
  align-items: center;
}

.form-group_inline .form-group__label, .form-group_table .form-group__label {
  padding-right: 15px;
}

.form-group_inline .form-group__field, .form-group_table .form-group__field {
  flex-grow: 1;
  min-width: 0;
}

.form-group_inline .form-group__delete, .form-group_table .form-group__delete {
  position: absolute;
  top: 0;
  right: 0;
}

.form-group_inline .form-group__delete ~ .form-group__field, .form-group_table .form-group__delete ~ .form-group__field {
  padding-right: 60px;
}

.form-group_column .form-group__field {
  width: 100%;
}

.form-group_table .form-group__label {
  flex-grow: 1;
  width: 33%;
}

.form-group_table .form-group__field {
  width: 57%;
}

.form-group__label {
  padding: 10px 0;
}

.form-group__field {
  position: relative;
}

.form-group__error-message {
  position: absolute;
  z-index: 10;
  left: 0;
  bottom: 100%;
  margin-bottom: 6px;
}

.add-item {
  display: inline-block;
  vertical-align: top;
}

.add-item__link {
  position: relative;
  display: block;
  padding-left: 20px;
  color: #2e99db;
  font-size: 18px;
  line-height: 1.2;
