}

.datepicker__next {
  float: right;
  border-radius: 0 4px 0 0;
}

.datepicker__title {
  overflow: hidden;
  font-size: 16px;
  font-weight: 500;
  line-height: 50px;
}

.datepicker__body {
  padding: 0 10px 10px;
}

.datepicker__table {
  display: table;
