.datepicker__thead {
  display: table-header-group;
}

.datepicker__tbody {
  display: table-row-group;
}

.datepicker__tr {
  display: table-row;
}

.datepicker__th, .datepicker__td {
  display: table-cell;
  vertical-align: top;
  text-align: center;
}

.datepicker__th {
  padding: 5px 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
  text-transform: capitalize;
}

.datepicker__td {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  text-transform: uppercase;
}

.datepicker__day {
  display: block;
  padding: 5px 0;
  color: #333;
  text-decoration: none;
  transition: background 0.25s;
