  padding: 10px 0;
  font-size: 28px;
  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
}

.footer {
  padding: 20px 40px;
  border-top: 1px solid rgba(51, 51, 51, 0.2);
}

.footer__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__left, .footer__right {
  width: 50%;
  font-size: 0;
}

.footer__left {
  float: left;
  text-align: left;
}

.footer__right {
  float: right;
  text-align: right;
}

.footer .btn:not(:last-child) {
  margin-right: 15px;
}

.modal {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  background: rgba(51, 51, 51, 0.9);
  transition: opacity 0.3s, visibility 0.3s;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
}

.modal_md .modal__container {
  max-width: 600px;
}

.modal_sm .modal__container {
  max-width: 400px;
}

.modal__container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(100% - 40px);
  max-width: 980px;
  transform: translate3d(-50%, -50%, 0);
}

[data-modal-target],
[data-modal-close] {
  cursor: pointer;
}

.modal-content {
  color: #333;
  background: #fff;
  box-shadow: 0 0 7px 2px rgba(0, 0, 0, 0.4);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
}

.modal-content__header {
  position: relative;
  padding: 15px 20px;
  color: #fff;
  background: #2e99db;
}

.modal-content__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background: none;
  text-align: center;
  font-size: 0;
  transition: background 0.2s, box-shadow 0.2s;
}

.modal-content__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal-content__close:hover {
  background: #1f7db7;
}

.modal-content__close:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.modal-content__close ~ .modal-content__title {
  padding-right: 60px;
}

.modal-content__close .icon {
  display: inline-block;
  vertical-align: middle;
  fill: #fff;
  font-size: 20px;
}

.modal-content__title {
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
  text-align: left;
}

.modal-content__footer {
  padding: 20px;
  border-top: 1px solid rgba(51, 51, 51, 0.2);
  font-size: 0;
  text-align: right;
}

.modal-content__footer .btn + .btn {
  margin-left: 20px;
}

.modal-content__body {
  overflow: auto;
  max-height: calc(100vh - 201px);
}

.modal-content__body-in {
  padding: 40px 20px;
}

.modal-content .form-group__label {
  font-size: 18px;
}

.modal-content__subtitle {
  margin-bottom: 10px;
  font-size: 22px;
  line-height: 1.2;
}

.modal-content__date-form {
  margin-bottom: 20px;
}

.modal-content__table {
  overflow: hidden;
}

.details__item {
