.account-details {
	font-size: 0;
	&__col {
		display: inline-block;
		vertical-align: top;
		width: 33.33%; }
	&__title,
	&__value,
	&__status {
		display: inline-block;
		vertical-align: top; }
	&__title {
		padding-top: 1px;
		padding-bottom: 1px;
		margin-right: 10px;
		@include f(16px, 700, 1.3); }
	&__value {
		padding: 2px 0;
		color: $blue;
		@include f(14px, 400, 1.3); }
	&__status {
		padding: 2px 6px;
		border-radius: 3px;
		color: $white;
		background: #f0ad4e;
		@include f(14px, 400, 1.3); } }
